import React, { Component } from 'react';
import logo from '../maice_logo.png';

class Header extends Component {
  render() {
    return (
      <div className="navbar has-shadow is-spaced">
        <img src={logo} className="maice marketing" alt="logo" style={{height: '40px'}} />
        <div className="buttons has-addons left-auto">
            <span className={`button ${this.props.activeTab === 'editor' ? 'is-info is-selected' : ''}`} onClick={this.props.SwitchEditorMode('editor')}>Full Editor</span>
            <span className={`button ${this.props.activeTab === 'research' ? 'is-info is-selected' : ''}`} onClick={this.props.SwitchEditorMode('research')}>Research Mode</span>
        </div>
      </div>
    );
  }
}

export default Header;



