import React, { Component } from 'react';
import './App.css';
import 'draft-js/dist/Draft.css';
import 'medium-draft/lib/index.css';

import Header from './Header';
import SidePanel from './SidePanel';


class App extends Component {

  state = {
    activeTab: 'editor',
    editorWidth: 300
  }

  SwitchEditorMode = (tab) => () => this.setState( {activeTab: tab} );
  SetEditorWidth = (e,data) => this.setState( {editorWidth: data.size.width} );

  render() {
    return (
      <div className="App">
        <Header SwitchEditorMode= {this.SwitchEditorMode} activeTab={this.state.activeTab} />
        <div className={`container is-fluid flexContainer ${this.state.activeTab === 'editor' ? 'hideResize' : ''}`}>
          <div style={{flex: `0 0 ${this.state.editorWidth}px`}}>
            <SidePanel activeTab={this.state.activeTab} SetEditorWidth={this.SetEditorWidth} />
          </div>
          {this.state.activeTab === 'research' ? <div className="researchDiv">
            <iframe width="100%" src="https://www.wikipedia.org/" title="test"></iframe>
          </div> : ''}
        </div>
      </div>
    );
  }
}

export default App;



